import React, { useState } from "react";
import { createPublicClient, http } from "viem";
import { polygonZkEvmCardona } from "viem/chains";
import { CONTRACT_ADDRESS, ABI } from "./constants";

// Viem-Client einrichten
const client = createPublicClient({
  chain: polygonZkEvmCardona,
  transport: http("https://polygonzkevm-cardona.g.alchemy.com/v2/_b_VRMPb-jVhNG10s4RLwDCEjQkPz4h1"),
});

function GetRoastData() {
  const [batchNumber, setBatchNumber] = useState("");
  const [roastData, setRoastData] = useState(null);
  const [errorMessage, setErrorMessage] = useState("");

  const getRoastData = async () => {
    if (!batchNumber) {
      setErrorMessage("Bitte geben Sie eine Chargennummer ein.");
      return;
    }

    setErrorMessage("");
    setRoastData(null);

    try {
      // Abrufen von Daten vom Smart Contract
      const data = await client.readContract({
        address: CONTRACT_ADDRESS,
        abi: ABI,
        functionName: "getRoastBatch",
        args: [batchNumber],
      });

      // Strukturierung der Daten und Zustand aktualisieren
      setRoastData({
        batchNumber: data[0],
        beanOrigin: data[1],
        beanType: data[2],
        roastDate: data[3],
        roastDuration: data[4],
        temperatureProfile: data[5],
        roastingMachine: data[6],
      });
    } catch (error) {
      console.error(error);
      setErrorMessage("Fehler beim Abrufen der Daten.");
    }
  };

  return (
    <div>
      <h2>Rstdaten abrufen</h2>
      <input
        type="text"
        placeholder="Chargennummer eingeben"
        value={batchNumber}
        onChange={(e) => setBatchNumber(e.target.value)}
        style={{ width: "300px", padding: "8px", marginRight: "10px" }}
      />
      <button onClick={getRoastData} style={{ padding: "8px 16px" }}>
        Abrufen
      </button>

      {errorMessage && (
        <p style={{ color: "red", marginTop: "20px" }}>{errorMessage}</p>
      )}

      {roastData && (
        <div style={{ marginTop: "20px" }}>
          <h3>Rstdaten fr Charge {roastData.batchNumber}</h3>
          <p>
            <strong>Bohnenherkunft:</strong> {roastData.beanOrigin}
          </p>
          <p>
            <strong>Bohnensorte:</strong> {roastData.beanType}
          </p>
          <p>
            <strong>Rstdatum:</strong> {roastData.roastDate}
          </p>
          <p>
            <strong>Rstdauer:</strong> {roastData.roastDuration}
          </p>
          <p>
            <strong>Temperaturprofil:</strong> {roastData.temperatureProfile}
          </p>
          <p>
            <strong>Rstmaschine:</strong> {roastData.roastingMachine}
          </p>
        </div>
      )}
    </div>
  );
}

export default GetRoastData;
